.root_header{
	background: rgba(255,255,255,0.8);
	padding: 0px 0px;
	height: var(--header_height);
	line-height: var(--header_height);
	position: fixed;
  -webkit-transition: -webkit-transform 0.4s;
  transition: -webkit-transform 0.4s;
  -o-transition: transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
  -webkit-transform: translateY(calc(var(--header_height)*-1.1));
      -ms-transform: translateY(calc(var(--header_height)*-1.1));
          transform: translateY(calc(var(--header_height)*-1.1));
}

.root_header.header_show{
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.header_link_container{
	display: table;
	table-layout: fixed;
  width: 100%;
}

.header_link_container li{
	display: table-cell;
  text-align: center;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.header_link_container li a,
.modalContainer li a{
	text-decoration: none;
	color: #888;
	font-size: 15px;
  -webkit-transition: color 0.2s;
  -o-transition: color 0.2s;
  transition: color 0.2s;
  display: block;
  position: relative;
}

.borderSpan{
  background-color: transparent;
  width: 10px;
  height: 10px;
  display: inline-block;
  position: absolute;
  bottom: 2px;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  border-radius: 50%;
}

.mobile_menu{
  position: fixed;
  width: 32px;
  height: 28px;
  top: 30px;
  right: 30px;
  display: none;
  -webkit-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  z-index: 6;
  cursor: pointer;
}

.mobile_menu span {
  display: block;
  position: absolute;
  height: 5px;
  width: 100%;
  background: #FFF;
  border: 2px solid #444;
  border-radius: 0px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
      transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

.mobile_menu span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
      -ms-transform-origin: left center;
          transform-origin: left center;
}

.mobile_menu span:nth-child(2) {
  top: 12px;
  -webkit-transform-origin: left center;
      -ms-transform-origin: left center;
          transform-origin: left center;
}

.mobile_menu span:nth-child(3) {
  top: 24px;
  -webkit-transform-origin: left center;
      -ms-transform-origin: left center;
          transform-origin: left center;
}

.mobile_menu.open span{
  background: #444;
  height: 4px;
}

.mobile_menu.open span:nth-of-type(1) {
  -webkit-transform: translateX(6px) translateY(27px) rotate(-45deg);
  -ms-transform: translateX(5px) translateY(24px) rotate(-45deg);
      transform: translateX(5px) translateY(24px) rotate(-45deg);
}
.mobile_menu.open span:nth-of-type(2) {
  opacity: 0;
}
.mobile_menu.open span:nth-of-type(3) {
  -webkit-transform: translateX(5px) translateY(-29px) rotate(45deg);
  -ms-transform: translateX(5px) translateY(-23px) rotate(45deg);
      transform: translateX(5px) translateY(-23px) rotate(45deg);
}

.modalContainer{
  opacity: 0;
  pointer-events: none;
}

@media(max-width:767px){
  .root_header{
    display: none;
  }
  .mobile_menu{
    display: block;
  }

  .modalContainer{
    position: fixed;
    height: 100%;
    width:100%;
    background-color: #FFF;
    z-index: 5;
    overflow-y: auto;
    pointer-events: none;
    -webkit-transform :translate3d(0px, 0px, 0px) scale(1.1);
            transform :translate3d(0px, 0px, 0px) scale(1.1);
    opacity: 0;
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;
  }
  
  .modalShow{
    pointer-events: inherit;
    -webkit-transform :translate3d(0px, 0px, 0px) scale(1);
            transform :translate3d(0px, 0px, 0px) scale(1);
    opacity: 1;
  }

  .modalContainer li{
    height:120px;
    line-height: 120px;
    text-align: center;
    position: relative;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-top: 1px solid #ddd;
    /* display: inline-block; */
  }

  .modalContainer ul li:first-child{
    border-width: 0;
  }
  
  .modalContainer li a{
    font-size: 18px;
    text-decoration: none;
  }
  
  .modalContainer .borderSpan{
    bottom:auto;
    width:56px;
    height:56px;
    top: 50%;
    left: 50%;
    z-index: -1;
    background-color: transparent!important;
    border:2px dashed transparent;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
  }
  
}


